// ** React Imports
import { useState } from 'react'
import { useDispatch, useSelector } from "react-redux"

// ** Third Party Components
import classnames from 'classnames'
import Flatpickr from 'react-flatpickr'
import { MapPin, User } from 'react-feather'
import 'cleave.js/dist/addons/cleave-phone.us'
import { Controller, useForm } from 'react-hook-form'
import { Button, Col, CustomInput, Form, FormGroup, Label, Row } from 'reactstrap'

//Custom Components
import InputGroup from '@components/input-group'
import UILoader from '@components/ui-loader'

// ** Styles
import '@styles/react/libs/flatpickr/flatpickr.scss'

// ** Store Actions
import { updateVendorEmployee, vendorEmployeeEditChange } from '@store/Vendors/actions'

const EmployeeInfoTab = () => {
  // ** State
  const store = useSelector(state => state.vendors),
    dispatch = useDispatch()
  const { selectedVendorEmployee, formErrors, isLoading, isSubmitting } = store
  // ** React hook form vars
  const { register, errors, handleSubmit, control, setValue, trigger } = useForm({
    defaultValues: { gender: 'gender-female', dob: null }
  })

  const handleFormChange = (name, value) => {
    dispatch(vendorEmployeeEditChange(name, value))
  }

  const onChangeTerms = (term) => {
    let { contactOptions } = selectedVendorEmployee
    if (contactOptions !== undefined) {
      const termIndex = contactOptions.indexOf(term)
      if (termIndex > -1) {
        contactOptions.splice(termIndex, 1)
      } else {
        contactOptions.push(term)
      }
    } else {
      contactOptions = [term]
    }

    handleFormChange('contactOptions', contactOptions)
  }

  const getBirthday = () => {
    const { birthday } = selectedVendorEmployee
    if (birthday === undefined) return new Date()
    return new Date(birthday * 1000)
  }


  const { physicalAddress, estate, county, country, birthday } = selectedVendorEmployee
  return (
    <Form
      onSubmit={handleSubmit(data => {
        dispatch(updateVendorEmployee(selectedVendorEmployee._id))
      })}
    >
      <Row className='mt-1'>
        <Col sm='12'>
          <h4 className='mb-1'>
            <User size={20} className='mr-50' />
            <span className='align-middle'>Personal Information</span>
          </h4>
        </Col>
        <Col lg='4' md='6'>
          <FormGroup>
            <Label className='d-block' for='dob'>
              Date of birth
            </Label>
            <Controller
              id='dob'
              name='dob'
              control={control}
              render={props => {
                return (
                  <Flatpickr
                    name={props.name}
                    placeholder='YYYY-MM-DD'
                    options={{ dateFormat: 'Y-m-d' }}
                    className={classnames('form-control', {
                      'is-invalid': errors.dob
                    })}
                    value={getBirthday()}
                    onChange={(date) => {
                      handleFormChange('birthday', Math.round(date[0].getTime() / 1000))
                    }}
                  />
                )
              }}
            />
          </FormGroup>
        </Col>
        <Col lg='4' md='6'>
          <FormGroup>
            <label className='d-block mb-1'>Gender</label>
            <FormGroup>
              <Controller
                name='gender'
                control={control}
                render={props => {
                  return (
                    <CustomInput
                      inline
                      type='radio'
                      label='Male'
                      value='Male'
                      id='gender-male'
                      name={props.name}
                      defaultChecked={selectedVendorEmployee.gender !== undefined && selectedVendorEmployee.gender === 'male'}
                      invalid={(selectedVendorEmployee.gender === undefined || selectedVendorEmployee.gender === null)}
                      onChange={() => {
                        handleFormChange('gender', 'male')
                      }}
                    />
                  )
                }}
              />
              <Controller
                name='gender'
                control={control}
                render={props => {
                  return (
                    <CustomInput
                      inline
                      type='radio'
                      label='Female'
                      value='Female'
                      id='gender-female'
                      name={props.name}
                      defaultChecked={selectedVendorEmployee.gender !== undefined && selectedVendorEmployee.gender === 'female'}
                      invalid={(selectedVendorEmployee.gender === undefined || selectedVendorEmployee.gender === null)}
                      onChange={() => {
                        handleFormChange('gender', 'female')
                      }}
                    />
                  )
                }}
              />
            </FormGroup>
          </FormGroup>
        </Col>
        <Col lg='4' md='6'>
          <FormGroup>
            <label className='d-block mb-1'>Contact Options</label>
            <FormGroup>
              <CustomInput
                inline
                type='checkbox'
                name='terms'
                id='emailTerms'
                value='Email'
                label='Email'
                defaultChecked={selectedVendorEmployee.contactOptions !== undefined && selectedVendorEmployee.contactOptions.includes('email')}
                onChange={() => {
                  onChangeTerms('email')
                }}
              />
              <CustomInput
                inline
                type='checkbox'
                name='terms'
                id='msgTerms'
                value='Messages'
                label='Messages'
                defaultChecked={selectedVendorEmployee.contactOptions !== undefined && selectedVendorEmployee.contactOptions.includes('message')}
                onChange={() => {
                  onChangeTerms('message')
                }}
              />
              <CustomInput
                inline
                type='checkbox'
                name='terms'
                id='phoneTerms'
                value='Phone'
                label='Phone'
                defaultChecked={selectedVendorEmployee.contactOptions !== undefined && selectedVendorEmployee.contactOptions.includes('phone')}
                onChange={() => {
                  onChangeTerms('phone')
                }}
              />
            </FormGroup>
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col sm='12'>
          <h4 className='mb-1 mt-2'>
            <MapPin size={20} className='mr-50' />
            <span className='align-middle'>Address</span>
          </h4>
        </Col>
        <Col lg='12' md='12'>
          <InputGroup
            type={'text'}
            label={'Physical Address'}
            error={formErrors['physicalAddress']}
            name={'physicalAddress'}
            placeholder={'Avalon apartments, Nairobi'}
            onInputChange={(name, value) => {
              handleFormChange(name, value)
            }}
            defaultValue={physicalAddress}
            className={classnames({ 'is-invalid': formErrors['physicalAddress'] })}
          />
        </Col>
        <Col lg='4' md='6'>
          <InputGroup
            type={'text'}
            label={'Estate'}
            error={formErrors['estate']}
            name={'estate'}
            placeholder={'Garden Estate'}
            onInputChange={(name, value) => {
              handleFormChange(name, value)
            }}
            defaultValue={estate}
            className={classnames({ 'is-invalid': formErrors['estate'] })}
          />
        </Col>
        <Col lg='4' md='6'>
          <InputGroup
            type={'text'}
            label={'County'}
            error={formErrors['county']}
            name={'county'}
            placeholder={'Nairobi'}
            onInputChange={(name, value) => {
              handleFormChange(name, value)
            }}
            defaultValue={county}
            className={classnames({ 'is-invalid': formErrors['county'] })}
          />
        </Col>
        <Col lg='4' md='6'>
          <InputGroup
            type={'text'}
            label={'Country'}
            error={formErrors['country']}
            name={'country'}
            placeholder={'Kenya'}
            onInputChange={(name, value) => {
              handleFormChange(name, value)
            }}
            defaultValue={country}
            className={classnames({ 'is-invalid': formErrors['country'] })}
          />
        </Col>
        <Col className='d-flex flex-sm-row flex-column mt-2'>
          <UILoader blocking={isLoading}>
            <Button type='submit' color='primary' className='mb-1 mb-sm-0 mr-0'>
              Save Changes
            </Button>
          </UILoader>
        </Col>
      </Row>
    </Form>
  )
}

export default EmployeeInfoTab
