// ** React Imports
import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** Employee Edit Components
import SocialTab from './Social'
import AccountTab from './Account'
import InfoTab from './Information'

// ** Store & Actions
import { getVendorEmployee } from '@store/Vendors/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import { Info, Share2, User } from 'react-feather'
import { Alert, Card, CardBody, Col, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap'

// ** Styles
import '@styles/react/apps/app-users.scss'

const EmployeeEdit = () => {
  // ** States & Vars
  const [activeTab, setActiveTab] = useState('1'),
    dispatch = useDispatch(),
    { isLoading, selectedVendorEmployee } = useSelector(state => state.vendors),
    { id, vendorId } = useParams()

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

  // ** Function to get employee on mount
  useEffect(() => {
    dispatch(getVendorEmployee(id, vendorId))
  }, [dispatch])

  return selectedVendorEmployee !== null && selectedVendorEmployee !== undefined ? (
    <Row className='app-user-edit'>
      <Col sm='12'>
        <Card>
          <CardBody className='pt-2'>
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === '1'} onClick={() => toggle('1')}>
                  <User size={14} />
                  <span className='align-middle d-none d-sm-block'>Account</span>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink active={activeTab === '2'} onClick={() => toggle('2')}>
                  <Info size={14} />
                  <span className='align-middle d-none d-sm-block'>Information</span>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink active={activeTab === '3'} onClick={() => toggle('3')}>
                  <Share2 size={14} />
                  <span className='align-middle d-none d-sm-block'>Social</span>
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId='1'>
                <AccountTab selectedVendorEmployee={selectedVendorEmployee} />
              </TabPane>
              <TabPane tabId='2'>
                <InfoTab />
              </TabPane>
              <TabPane tabId='3'>
                <SocialTab />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
      </Col>
    </Row>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>Vendor employee not found</h4>
      <div className='alert-body'>
        Vendor Employee with id: {id} doesn't exist. Check list of all Vendor Employees: <Link to={`/vendors/view/${vendorId}`}>Vendor Employees List</Link>
      </div>
    </Alert>
  )
}

export default EmployeeEdit
