// ** Third Party Components
import { Button, Col, Form, FormGroup, Input, InputGroup, InputGroupAddon, InputGroupText, Label, Row } from 'reactstrap'
import { useDispatch, useSelector } from "react-redux"
import { Facebook, Instagram, Twitter } from 'react-feather'

// ** Custom components
import UILoader from '@components/ui-loader'

// ** Store Actions
import { updateVendorEmployee, vendorEmployeeEditChange } from '@store/Vendors/actions'

const EmployeeSocialTab = () => {

  const dispatch = useDispatch(),
    { formErrors, isLoading, selectedVendorEmployee } = useSelector(state => state.vendors)

  const handleFormChange = (name, value) => {
    dispatch(vendorEmployeeEditChange(name, value))
  }
  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(updateVendorEmployee(selectedVendorEmployee._id))
  }

  const { twitter, facebook, instagram } = selectedVendorEmployee
  return (
    <Form onSubmit={handleSubmit}>
      <Row>
        <Col lg='4' md='6' sm='12'>
          <Label for='twitter'>Twitter</Label>
          <FormGroup tag={InputGroup} className='input-group-merge'>
            <InputGroupAddon addonType='prepend'>
              <InputGroupText>
                <Twitter size={17} />
              </InputGroupText>
            </InputGroupAddon>
            <Input
              id='twitter'
              placeholder='https://www.twitter.com/'
              type={'text'}
              name={'twitter'}
              onChange={e => {
                handleFormChange(e.target.name, e.target.value)
              }}
              value={twitter || 'https://www.twitter.com/'}
            />
          </FormGroup>
        </Col>
        <Col lg='4' md='6' sm='12'>
          <Label for='facebook'>Facebook</Label>
          <FormGroup tag={InputGroup} className='input-group-merge'>
            <InputGroupAddon addonType='prepend'>
              <InputGroupText>
                <Facebook size={17} />
              </InputGroupText>
            </InputGroupAddon>
            <Input
              id='facebook'
              placeholder='https://www.facebook.com/'
              type={'text'}
              name={'facebook'}
              onChange={e => {
                handleFormChange(e.target.name, e.target.value)
              }}
              value={facebook || 'https://www.facebook.com/'}
            />
          </FormGroup>
        </Col>
        <Col lg='4' md='6' sm='12'>
          <Label for='instagram'>Instagram</Label>
          <FormGroup tag={InputGroup} className='input-group-merge'>
            <InputGroupAddon addonType='prepend'>
              <InputGroupText>
                <Instagram size={17} />
              </InputGroupText>
            </InputGroupAddon>
            <Input
              id='instagram'
              placeholder='https://www.instagram.com/'
              type={'text'}
              name={'instagram'}
              onChange={e => {
                handleFormChange(e.target.name, e.target.value)
              }}
              value={instagram || 'https://www.instagram.com/'}
            />
          </FormGroup>
        </Col>
        <Col className='d-flex flex-sm-row flex-column mt-2' sm='12'>
          <UILoader blocking={isLoading}>
            <Button className='mb-1 mb-sm-0 mr-0' color='primary'>
              Save Changes
            </Button>
          </UILoader>
        </Col>
      </Row>
    </Form>
  )
}

export default EmployeeSocialTab
