// ** React Imports
import { useState } from 'react'
import { useDispatch, useSelector } from "react-redux"
import classnames from "classnames"

// ** Custom Components
import UserEditAvatar from '@components/user-avatar/UserEditAvatar'
import PermissionsTable from '@components/table/PermissionsTable'
import InputGroup from '@components/input-group'
import UILoader from '@components/ui-loader'

// ** Third Party Components
import { Button, Col, Form, Row } from 'reactstrap'

// ** Store Actions
import { updateVendorEmployee, vendorEmployeeEditChange } from '@store/Vendors/actions'
// ** Utils UploadFile
import uploadFile from '@utils/uploadFile'

const EmployeeAccountTab = () => {
  // ** States
  const [imgLoading, setImgLoading] = useState(false)

  const dispatch = useDispatch(),
    { formErrors, isLoading, selectedVendorEmployee } = useSelector(state => state.vendors)
  const { rolesConfig } = useSelector(state => state.roles)
  const handleFormChange = (name, value) => {
    dispatch(vendorEmployeeEditChange(name, value))
  }

  // ** Function to change employee image
  const onChange = async e => {
    const files = e.target.files
    setImgLoading(true)
    try {
      const url = await uploadFile(files[0])
      handleFormChange('profilePictureURL', url)
      setImgLoading(false)
    } catch (e) {
      setImgLoading(false)
    }
  }

  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(updateVendorEmployee(selectedVendorEmployee._id))
  }

  const { roles, status } = rolesConfig.vendorEmployee
  const roleOptions = Object.keys(roles).map((mKey) => {
    return <option key={mKey} value={mKey}>{roles[mKey]}</option>
  })

  const statusOptions = Object.keys(status).map((sKey) => {
    return <option key={sKey} value={sKey}>{status[sKey]}</option>
  })

  return (
    <Row>
      <Col sm='12'>
        <UserEditAvatar
          name={`${selectedVendorEmployee.firstName} ${selectedVendorEmployee.lastName}`}
          photo={selectedVendorEmployee.profilePictureURL}
          imgLoading={imgLoading}
          onChangeAvatar={onChange}
        />
      </Col>
      <Col sm='12'>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'First Name'}
                error={formErrors['firstName']}
                name={'firstName'}
                placeholder={'First Name'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendorEmployee.firstName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['firstName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Last Name'}
                error={formErrors['lastName']}
                name={'lastName'}
                placeholder={'Last Name'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendorEmployee.lastName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['lastName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Username'}
                error={formErrors['userName']}
                name={'userName'}
                placeholder={'Username'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendorEmployee.userName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['userName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Email'}
                error={formErrors['email']}
                name={'email'}
                placeholder={'Email'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendorEmployee.email}
                required={true}
                className={classnames({ 'is-invalid': formErrors['email'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Country Code'}
                error={formErrors['countryCode']}
                name={'countryCode'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendorEmployee.countryCode}
                required={true}
                className={classnames({ 'is-invalid': formErrors['countryCode'] })}
              >
                <option value=''>Select Country Code</option>
                <option value='254'>+254 Kenya</option>
              </InputGroup>
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'number'}
                label={'Phone Number'}
                error={formErrors['phoneNumber']}
                name={'phoneNumber'}
                placeholder={'Phone Number'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendorEmployee.phoneNumber}
                required={true}
                className={classnames({ 'is-invalid': formErrors['phoneNumber'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Employee Role'}
                error={formErrors['role']}
                name={'role'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendorEmployee.role}
                required={true}
                className={classnames({ 'is-invalid': formErrors['role'] })}
              >
                <option value=''>Select Role</option>
                {roleOptions}
              </InputGroup>
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Status'}
                error={formErrors['status']}
                name={'status'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendorEmployee.status}
                className={classnames({ 'is-invalid': formErrors['status'] })}
              >
                <option value=''>Select Status</option>
                {statusOptions}
              </InputGroup>
            </Col>
            <Col sm='12'>
              <PermissionsTable />
            </Col>
            <Col className='d-flex flex-sm-row flex-column mt-2' sm='12'>
              <UILoader blocking={isLoading} className='mr-sm-1'>
                <Button.Ripple className='mb-1 mb-sm-0 mr-0' type='submit' color='primary'>
                  Save Changes
                </Button.Ripple>
              </UILoader>
              <Button.Ripple color='secondary' outline>
                Reset
              </Button.Ripple>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  )
}

export default EmployeeAccountTab
