// ** React Imports
import PropTypes from 'prop-types'

// ** Custom Components
import Avatar from '@components/avatar'
import UILoader from '@components/ui-loader'
import { Button, Label, Media } from "reactstrap"
import { Edit, Trash2 } from "react-feather"


const EditAvatar = (props) => {
  const { imgLoading, name, photo } = props
  if (photo === undefined || photo === '') {
    const stateNum = Math.floor(Math.random() * 6),
      states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
      color = states[stateNum]
    return (
      <UILoader blocking={imgLoading}>
        <Avatar
          initials
          color={color}
          className='rounded mr-2 my-25'
          content={name}
          contentStyles={{
            borderRadius: 0,
            fontSize: 'calc(36px)',
            width: '100%',
            height: '100%'
          }}
          style={{
            height: '90px',
            width: '90px'
          }}
        />
      </UILoader>
    )
  } else {
    return (
      <UILoader blocking={imgLoading}>
        <img
          className='user-avatar rounded mr-2 my-25 cursor-pointer'
          src={photo}
          alt='user profile avatar'
          height='90'
          width='90'
        />
      </UILoader>
    )
  }
}


const UserEditAvatar = (props) => {
  const { imgLoading, name, photo, onChangeAvatar } = props
  return (
    <Media className='mb-2'>
      <EditAvatar
        imgLoading={imgLoading}
        name={name}
        photo={photo}
      />
      <Media className='mt-50' body>
        <h4>{name} </h4>
        <div className='d-flex flex-wrap mt-1 px-0'>
          <UILoader blocking={imgLoading} className='mr-75'>
            <Button.Ripple id='change-img' tag={Label} className='mb-0' color='primary'>
              <span className='d-none d-sm-block'>Change</span>
              <span className='d-block d-sm-none'>
                  <Edit size={14} />
                </span>
              <input type='file' hidden id='change-img' onChange={onChangeAvatar} accept='image/*' />
            </Button.Ripple>
          </UILoader>
          <Button.Ripple color='secondary' outline>
            <span className='d-none d-sm-block'>Remove</span>
            <span className='d-block d-sm-none'>
                  <Trash2 size={14} />
                </span>
          </Button.Ripple>
        </div>
      </Media>
    </Media>
  )
}

export default UserEditAvatar

// ** Default Props
UserEditAvatar.defaultProps = {
  imgLoading: false,
  name: '',
  photo: '',
  onChangeAvatar: () => false
}

// ** PropTypes
UserEditAvatar.propTypes = {
  imgLoading: PropTypes.bool,
  name: PropTypes.string.isRequired,
  photo: PropTypes.string,
  onChangeAvatar: PropTypes.func
}
